import { useRef, useEffect, useState } from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import '../node_modules/image-viewer/dist/singleImage/singleImage.iife.min.js'
import testImage from './test_image.jpg';

import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button'
import TextField from '@material-ui/core/TextField'
import AssignmentIcon from '@material-ui/icons/Assignment';

const useStyles = makeStyles((theme) => ({
  app: {
    height: '100vh',
    display: 'flex',
    flexDirection: 'row'
  },
  imageViewer: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column'
  },
  imageBar: {
    display: 'flex',
    flexDirection: 'row',
    padding: theme.spacing(0.5),
    alignItems: 'center',
    '& *' : {
      margin: theme.spacing(0.2)
    }
  },
  editor: {
    borderLeft: '1px solid #ccc',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    width: 250,
    padding: theme.spacing(0.5),
    '& *' : {
      margin: theme.spacing(0.2)
    }
  },
  pad: {
    height: 50,
  },
  coords: {
    display: 'flex',
    flexDirection: 'row',
    margin: 0,
    paddingLeft: theme.spacing(5),
    backgroundColor: '#eee',
    paddingTop: theme.spacing(10),
    paddingBottom: theme.spacing(10)
  },
  labels: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'flexEnd',
    textAlign: 'right',
    fontWeight: 700
  },
  values: {
    display: 'flex',
    flexDirection: 'column',
  },
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

const isNumber = something => {
  return (!isNaN(something) && something !== null && something !== undefined);
}

const padWithZeros = num => {
  const FIGS = 16;
  return `${num}`.padEnd(FIGS, '0').substr(0, FIGS);
}

let overlay = {
  id: 'overlay',
  x: 0.25, y: 0.25,
  width: 0.25, height: 0.25,
  movable: true,
  resizable: true,
  selectable: true,
  type: 'name'
};

const App = () => {
  const classes = useStyles();

  const [ coords, setCoords ] = useState({
    x1: overlay.x,
    y1: overlay.y,
    x2: overlay.x + overlay.width,
    y2: overlay.y + overlay.height
  });
  const [ setBtnDisabled, setSetBtnDisabled ] = useState(true);
  const [ loadBtnDisabled, setLoadBtnDisabled ] = useState(true);
  const [ nineFive, setNineFive ] = useState();
  const [ session, setSession ] = useState();
  const viewer = useRef();
  const sessionInput = useRef();
  const nineFiveInput = useRef();
  const coordsInput = useRef();
  const setButton = useRef();
  const loadButton = useRef();
  const clearCoordinatesButton = useRef();


  const onOverlayChange = evt => {
    if (evt.detail.overlays[overlay.id]) {
      const { x, y, width, height } = evt.detail.overlays[overlay.id];
      Object.assign(overlay, evt.detail.overlays[overlay.id]);
      setCoords({ x1: x, y1: y, x2: x + width, y2: y + height });
    }
  }

  useEffect(() => {
    // runs once when component mounts
    let viewerRef = null;
    if (viewer.current) {
      viewerRef = viewer.current;
      viewer.current.overlays.addAndSelect(overlay);
      viewerRef.addEventListener('fs-single-image-viewer:overlay-change', onOverlayChange);
    }

    //check for query string
    const urlSearchParams = new URLSearchParams(window.location.search);
    const params = Object.fromEntries(urlSearchParams.entries());
    
    if (params.source) {
      setNineFive(params.source)
      if (nineFiveInput.current) {
        nineFiveInput.current.value = params.source;
      }
    }

    const oldSession = window.sessionStorage.getItem('session');
    if (oldSession) {
      setSession(oldSession);
      if (sessionInput.current) {
        sessionInput.current.value = oldSession;
      }
    }

    return () => {
      viewerRef.removeEventListener('fs-single-image-viewer:overlay-change', onOverlayChange);
    };
  }, [])

  const handleGetSessionId = () => {
    window.open("https://www.familysearch.org/platform/", '_blank', 'noopener,noreferrer');
  }

  const handleLoad = () => {
    if (nineFiveInput.current && sessionInput.current) {
      const nextNineFive = nineFiveInput.current.value;
      setNineFive(nextNineFive);

      const nextSession = sessionInput.current.value;
      if (nextSession && nextSession !== session) {
        setSession(nextSession);
        window.sessionStorage.setItem('session', nextSession);
      }

      const urlSearchParams = new URLSearchParams(window.location.search);
      urlSearchParams.set('source', nextNineFive);
      const newUrl = `${window.location.protocol}//${window.location.host}${window.location.pathname}?${urlSearchParams.toString()}`;
      window.history.pushState({ path: newUrl }, '', newUrl);
    }
  }

  const handleNineFiveInputKeyPress = evt => {
    if(evt.key === 'Enter'){
      handleLoad();
      nineFiveInput.current.blur();
    }
  }

  const handleNineFiveInputChange = evt => {
    let nextLoadBtnDisabled = true;
    if (nineFiveInput.current && nineFiveInput.current.value) {
      const [group, imageNum] = nineFiveInput.current.value.split('_');
      if (isNumber(group) && isNumber(imageNum)) {
        nextLoadBtnDisabled = false;
      }
    }

    if (nextLoadBtnDisabled !== loadBtnDisabled) {
      setLoadBtnDisabled(nextLoadBtnDisabled);
    }
  }


  const handleSetCoords = () => {
    if (coordsInput.current) {
      if (coordsInput.current.value) {
        const [ x1, y1, x2, y2 ] = coordsInput.current.value.split(',').map(parseFloat);
        if (isNumber(x1) && isNumber(y1) && isNumber(x2) && isNumber(y2) && x2 > x1 && y2 > y1) {
          Object.assign(overlay, {
            x: (x1),
            y: (y1),
            width: (x2 - x1),
            height: (y2 - y1)
          });
          viewer.current.overlays.update(overlay);
          setCoords({ x1, y1, x2, y2 });
          coordsInput.current.value = null;
          setSetBtnDisabled(true);
          coordsInput.current.blur();
        }
      }
    }
  }

  const handleClearCoords = () => {
    coordsInput.current.value = null;
  }

  const handleCopyToClipboard = () => {
    if (coords) {
      const { x1, y1, x2, y2 } = coords;
      navigator.clipboard.writeText(`${padWithZeros(x1)}, ${padWithZeros(y1)}, ${padWithZeros(x2)}, ${padWithZeros(y2)}`);
    }
  }

  const handleCoordsInputChange = evt => {
    let nextSetBtnDisabled = true;
    if (coordsInput.current && coordsInput.current.value) {
      const [ x1, y1, x2, y2 ] = coordsInput.current.value.split(',').map(parseFloat);
      if (isNumber(x1) && isNumber(y1) && isNumber(x2) && isNumber(y2) && x2 > x1 && y2 > y1) {
        nextSetBtnDisabled = false;
      }  
    }

    if (nextSetBtnDisabled !== setBtnDisabled) {
      setSetBtnDisabled(nextSetBtnDisabled);
    }
  }

  const handleCoordInputKeyPress = evt => {
    if(evt.key === 'Enter'){
      handleSetCoords();
    }
  }

  const source = nineFive
    ? `https://www.familysearch.org/dz/v1/dgs:${nineFive}/image.xml?ctx=&session=${session}`
    : testImage
  ;

  return (
    <>
      <CssBaseline/>
      <div className={classes.app}>
        <div className={classes.imageViewer}>
          <div className={classes.imageBar}>
            <TextField id="sessionField" label="Session ID" variant="outlined" style={{width: 385}} inputRef={sessionInput}/>
            <Button variant="contained" color="primary" onClick={handleGetSessionId} >Get Session Id</Button>
            <TextField id="outlined-basic" label="Image Number" variant="outlined" onKeyPress={handleNineFiveInputKeyPress}onChange={handleNineFiveInputChange} inputRef={nineFiveInput}/>
            <Button variant="contained" color="primary" onClick={handleLoad}  ref={loadButton}  disabled={loadBtnDisabled}>Load Image</Button>
         </div>
          <fs-single-image-viewer
            ref={viewer}
            // src={testImage}
            src={source}
          ></fs-single-image-viewer>
        </div>
        <div className={classes.editor}>
          <TextField id="outlined-basic" label="x1, y1, x2, y2" variant="outlined" multiline={true} rows={4} onChange={handleCoordsInputChange} onKeyPress={handleCoordInputKeyPress} inputRef={coordsInput}/>
          <Button variant="contained" color="primary" onClick={handleSetCoords} disabled={setBtnDisabled} ref={setButton}>Set Box</Button>
          <Button variant="contained" color="primary" onClick={handleClearCoords} disabled={setBtnDisabled} ref={clearCoordinatesButton}>Clear Coordinates</Button>

          <div className={classes.coords}>
            <div className={classes.labels}>
              <label>x1:</label>
              <label>y1:</label>
              <label>x2:</label>
              <label>y2:</label>
            </div>
            <div className={classes.values}>
              <span>{padWithZeros(coords.x1)}</span>
              <span>{padWithZeros(coords.y1)}</span>
              <span>{padWithZeros(coords.x2)}</span>
              <span>{padWithZeros(coords.y2)}</span>
            </div>
          </div>

          <Button startIcon={<AssignmentIcon />} variant="outlined" color="primary" onClick={handleCopyToClipboard}>Copy To Clipboard</Button>
        </div>
      </div>

    </>
  );
}

export default App;
